.App {
  padding: 50px;
  box-sizing: border-box;
  position: relative;
}

.MoneyShowBox {
  width: 450px;
  height: 300px;
  background-color: rgba(255, 255, 255, 0.356);
  border-radius: 1em;
  margin: 50px;
  margin-left: 140px;
  box-sizing: border-box;
  padding: 15px;
}

.MoneyShowBox h3 {
  color: rgb(131, 131, 131);
  font-weight: 900;
}

.MoneyShowBoxIncomeFast {
  width: 420px;
  height: 100px;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}

.MoneyShowBoxIncomeFastSectionA {
  width: 210px;
  height: 50px;
  display: flex;
  justify-content: left;
  align-items: center;
  flex-wrap: wrap;
}
.MoneyShowBoxIncomeFastSectionB {
  width: 210px;
  height: 50px;
  display: flex;
  justify-content: left;
  align-items: center;
  flex-wrap: wrap;
}

.ArrowIncome {
  width: 15px;
  height: 15px;
  background-image: url('arrow-small-up.png');
  background-repeat: no-repeat;
  background-size: cover;
  margin-right: 10px;
}
.ArrowSpend {
  width: 15px;
  height: 15px;
  background-image: url('arrow-small-down.png');
  background-repeat: no-repeat;
  background-size: cover;
  margin-right: 10px;
}

.MoneyShowBoxIncomeFastSectionA p {
  color: rgb(131, 131, 131);
  font-weight: 900;
}

.AddFundsBox {
  width: 250px;
  height: 250px;
  background-color: rgba(255, 255, 255, 0.356);
  border-radius: 1em;
  margin: 50px;
  margin-left: 140px;
  box-sizing: border-box;
  padding: 15px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}

.FundsAddIcon {
  width: 150px;
  height: 150px;
  background-image: url(funds3d.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.AddFundsMenuTrigger {
  width: 150px;
  height: 60px;
  background: rgb(239,204,255);
  background: linear-gradient(90deg, rgb(214, 125, 255) 15%, rgb(185, 70, 252) 100%);
  background-size: 350%;
  background-position: 0px;
  border: none;
  color: white;
  border-radius: 1em;
  display: flex;
  justify-content: space-around;
  align-items: center;
  transition: ease-in-out 1s;
  cursor: pointer;
}
.AddFundsMenuTrigger:hover {
  background-position: 150px;
  background-size: 950%;
}

.PlusIcon {
  width: 10px;
  height: 10px;
  background-image: url(plus.png);
  background-repeat: no-repeat;
  background-size: cover;
}

@keyframes AddFundsMenuAnimationShow {

  0% { opacity: 0.5; scale: 0.1; transform: translate(-180%, -20%); }
  100% { opacity: 1; scale: 1; }
}

.AddFundsMenu {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 650px;
  height: 460px;
  background-color: rgb(255, 255, 255);
  border-radius: 1em;
  z-index: 1000;
  animation-name: AddFundsMenuAnimationShow;
  animation-duration: 0.3s;
  animation-timing-function: ease-in;
  animation-iteration-count: 1;
  text-align: center;
  text-wrap: wrap;
}

@keyframes AddFundsMenuAnimationClose {

  0% { opacity: 1; scale: 1; transform: translate(-50%, -50%);}
  100% { opacity: 0; scale: 0; transform: translate(-150%, -50%); }
}

.AddFundsMenu.fade-out {
  animation-name: AddFundsMenuAnimationClose;
  animation-duration: 0.5s;
  animation-timing-function: ease-in;
  animation-iteration-count: 1;
}

.AddFundsMenu input, select {
  width: 350px;
  height: 50px;
  border: none;
  outline: none;
  border-radius: 1em;
  box-sizing: border-box;
  padding: 15px;
  background-color: rgba(221, 221, 221, 0.35);
}

.AddFundsTrigger {
  margin-top: 30px;
  width: 305px;
  height: 50px;
  background-color: rgba(221, 221, 221, 0.35);
  border-radius: 1em;
  border: none;
  outline: none;
  cursor: pointer;
  transition: ease-in-out 0.3s;
}
.AddFundsTrigger:hover {
  background-color: rgba(177, 177, 177, 0.35);
}

.ExitButton {
  width: 15px;
  height: 15px;
  background-image: url(close.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-color: transparent;
  border: none;
  outline: none;
  position: absolute;
  right: 30px;
  top: 15px;
  transition: ease-in 0.2s;
  cursor: pointer;
}
.ExitButton:hover {
  scale: 0.8;
}

.LowerContainer {
  margin-top: 60px;
  width: 100%;
  height: 40vh;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
}

.TransactionsListBox {
  width: 1000px;
  height: 320px;
  background-color: rgba(255, 255, 255, 0.356);
  border-radius: 1em;
  padding: 15px;
  box-sizing: border-box;
  position: relative;
}
.TransactionsListBox h1{
  color: rgb(131, 131, 131);
  font-weight: 900;
}

.TransactionList {
  width: 970px;
  height: 200px;
  background-color: transparent;
  display: flex;
  align-items: flex-start;
  padding: 10px;
  box-sizing: border-box;
  justify-content: flex-start;
  flex-direction: column;
}
.TransactionList h3 {
  color: black;
}

.RedText {
  color: red;
}

.GreenText {
  color: rgb(0, 180, 0);
}

table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0 20px;
}

th, td {
  padding: 8px;
}

th {
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: transparent;
  color: black;
  padding: 8px;
  width: 220px;
}

th::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(5px);
  z-index: -1;
  pointer-events: none;
}

.BorderLeftStyle::before {
  border-top-left-radius: 1em;
  border-bottom-left-radius: 1em;
}
.BorderRightStyle::before {
  border-top-right-radius: 1em;
  border-bottom-right-radius: 1em;
}

tr {
  text-align: center;
}

.table-container {
  width: 970px;
  height: 200px;
  overflow-y: auto;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
::-webkit-scrollbar-button {
  width: 0px;
  height: 0px;
}
::-webkit-scrollbar-thumb {
  background: #d1d1d1;
  border: 0px none #ffffff;
  border-radius: 50px;
}
::-webkit-scrollbar-thumb:hover {
  background: #ababab;
}
::-webkit-scrollbar-thumb:active {
  background: #949494;
}
::-webkit-scrollbar-track {
  background: #ffffff;
  border: 0px none #ffffff;
  border-radius: 50px;
}
::-webkit-scrollbar-track:hover {
  background: #ffffff;
}
::-webkit-scrollbar-track:active {
  background: #ffffff;
}
::-webkit-scrollbar-corner {
  background: transparent;
}

.ClearButton {
  position: absolute;
  right: 15px;
  top: 15px;
  width: 125px;
  height: 25px;
  background-color: rgba(221, 221, 221, 0.35);
  border-radius: 1em;
  border: none;
  outline: none;
  transition: ease-in 0.2s;
  cursor: pointer;
}
.ClearButton:hover {
  background-color: rgba(221, 221, 221, 0.9);
}

.DeleteButton {
  width: 50px;
  height: 30px;
  background-color: rgba(221, 221, 221, 0.35);
  border-radius: 1em;
  border: none;
  outline: none;
  transition: ease-in 0.2s;
  cursor: pointer;
}
.DeleteButton:hover {
  background-color: rgba(233, 0, 0, 0.9);
  color: white;
}

.fade-enter {
  opacity: 0;
  transform: translateY(-10px);
}

.fade-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.fade-exit {
  opacity: 1;
  transform: translateY(0);
}

.fade-exit-active {
  opacity: 0;
  transform: translateY(-10px);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.UpperContainer {
  width: 100%;
  height: 40vh;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
}

.IncomeChartBox {
  width: 900px;
  height: 470px;
  position: relative;
  margin: 20px 0;
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, 0.356);
  border-radius: 1em;
}